.kzt_dialog-select {
  position: relative;
  text-decoration: none;
  color: #212121;
}
.kzt_dialog-select-btn {
  position: relative;
  display: inline-block;
  width: 48px;
  border: 1px solid var(--theme-color);
  border-radius: 2px;
  padding: 2px 6px;
  color: var(--theme-color);
  cursor: pointer;
}
.kzt_dialog-select .kzt_dialog-select-btn-text {
  font-size: 12px;
}
.kzt_dialog-select-btn-icon {
  margin-left: 4px;
  font-size: 12px;
}
.kzt_dialog-select .kzt_dialog-select-options {
  position: absolute;
  right: 0;
  bottom: 40px;
  width: 80px;
  background: #FFFFFF;
  box-shadow: 0px 1px 6px 0px rgba(0, 0, 0, 0.3);
  border-radius: 2px;
  overflow: hidden;
  z-index: 900;
}
.kzt_dialog-select-item {
  list-style: none;
  padding: 10px;
  text-align: left;
  color: #212121;
  cursor: pointer;
}
.kzt_dialog-select-item:hover {
  background-color: rgba(245, 245, 245, 0.4);
}
.kzt_dialog-select:hover,
.kzt_dialog-select:visited,
.kzt_dialog-select:link,
.kzt_dialog-select:active {
  color: #212121;
}
