














































.wtc-dialog-wrap {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 1;
  z-index: 9999;
}

.wtc-dialog-fade-enter-active,
.wtc-dialog-fade-leave-active {
  transition: opacity .5s;
}

.wtc-dialog-fade-enter,
.wtc-dialog-fade-leave-to {
  opacity: 0
}
